/** @jsx jsx */

import React from 'react';
import { jsx } from 'theme-ui';
import { Flex, Box, Heading, Text } from 'rebass';
import styled from '@emotion/styled/macro';
import strings from './strings.json';
import './collectionTab.css';

import MainPageCollectionBlock from './MainPageCollectionBlock';
import CatalogProducts from '../catalog/CatalogProducts';
import CollectionTab from './CollectionTab';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';

const { ariaTabHeader } = strings;

const StyledBox = styled(Box)`
  position: relative;
  z-index: 1;
`;

const MainPageSection = (props) => {
  const { tabs, data } = props;

  return (
    <Box className="mainPageTabs" mx={["5vw", "3.5vw"]} mt={["5vw", "3.5vw"]} mb={0} p={["5vw", "3.5vw"]} bg="backgroundGrey" textAlign="center">
      <Heading mb={"1rem"} sx={{ lineHeight: 'heading', textTransform: 'capitalize' }}>{ariaTabHeader}</Heading>
      <Tabs defaultActiveKey="face">
        {tabs.children.map((block, index) => {
          if (
            block.type === 'collection' &&
            data.collections.nodes.filter(
              (collection) => collection.handle === block.handle
            )[0]
          ) {
            return (
              <Tab eventKey={block.handle} title={block.name} key={index}>
                <Box>
                  <CollectionTab
                    collection={
                      data.collections.nodes.filter(
                        (collection) => collection.handle === block.handle
                      )[0]
                    }
                    data={data}
                  />
                </Box>
              </Tab>
            );
          } else {
            return '';
          }
        })}
      </Tabs>
    </Box>
  );
};

export default MainPageSection;
