/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Heading, Text } from 'rebass';
import styled from '@emotion/styled/macro';

const Seal = styled(Box)`
  position: relative;
  color: #00662E;
`;

const MainPageHeader = (props) => {
  const {
    theme: { breakpoints },
  } = useThemeUI();

  const { header } = props;
  const sectionItemsNumber =
    header && header.children && header.children.length > 0
      ? header.children.length
      : 1;
  const sectionHeight = sectionItemsNumber < 3 ? '60vh' : '40vh';

  return (
    <Box bg="background" textAlign="center" mx="auto" p={["5vw", "3.5vw"]}>
      <Seal>
        <Box sx={{ content: '""', background: 'url(https://cdn.shopify.com/s/files/1/0117/9005/7534/files/round-border.png) center/contain no-repeat',  height: ['60vw', '20vw', '15vw'], width: ['60vw', '20vw', '15vw'], position: ['relative', 'absolute'], transform: ['translate(-50%, 0)', 'translate(-50%, -50%)', 'translate(-50%, -50%)'], top: [0, '-15vw'], left: ['50%', 'unset'], right: ['unset', 0], zIndex: '2' }}>
          <Text sx={{ fontWeight: 'bold', lineHeight: 'body', position: 'relative', top: '50%', transform: 'translateY(-50%)' }}>
            NÂ°1<br/>
            Shea Butter<br/>
            Cosmetic Brand
          </Text>
        </Box>
      </Seal>
      <Flex flexWrap="wrap">
        {header.children.map((block, index) => {
          return (
            <Box
              bg="background"
              width={[1, 1 / sectionItemsNumber]}
              key={index}
              height={"auto"}
              p={[".3em", "1em"]}
              textAlign="center" sx={{ textShadow: '0 0 2px #323232' }}
            >
              <Text width={[1, "40%"]} fontSize={["7vw", "3vw"]} mx="auto">{block.header}</Text>
              <Text width={["95%", "30%"]} fontSize={["4vw", "1.3vw"]} mx="auto" my="1em">{block.subheader}</Text>
            </Box>
          );
        })}
      </Flex>
    </Box>
  );
};

export default MainPageHeader;
